//import React from 'react';
import Dexie from 'dexie';

// Create your instance
const photoDatabase = new Dexie("photoDatabase");

// Define your schema
photoDatabase.version(1).stores({
    photoStore: 'photoId, dataURL, uploadStatus'
});

// Open the photoDatabase
photoDatabase.open().catch(function (e) {
    console.error("Open failed: " + e);
});



export default photoDatabase;
