/*
CategoryList - Main working menu that shows categories

Purpose:
User clicks category in order to open their mobile camera.
User can choose to upload(?) photos

*/

import React from 'react';

import categoryListFile from '../../categoryListFile';

import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
//removed 'getLocalStorage'
import { readTextFile } from '../../actions';

import Footer from '../Footer';

//Used for filling in footer
const links = [
  { path: '/', id: 'newJob', label: 'New Job' }
]

class CategoryList extends React.Component{

  //If there is no localStorage, read text file and create categories.  Then save to localStorage
  //Otherwise, load categories from localStorage
  componentDidMount(){
    try{
      if(localStorage.getItem("state").includes(`"categories":{}`)){
        this.props.readTextFile(categoryListFile);
      }
    } catch (err) {
      //catch error.  If state is null, create new categories list
      this.props.readTextFile(categoryListFile);
    }
  }

  //Renders list of categories and sets each to be a link to individual category page
  renderList() {
    return this.props.categories.map( category => {
      return(
        <Link to={`/categories/${category.id}`} className="item" key={category.id}>
          <i className="large middle aligned icon camera" />
          <div className="content">
            {category.title}
            <div className="description">{category.description}</div>
          </div>
        </Link>
      );
    });
  }

  //Render component calling renderList
  render(){
    return(
      <div>
        <h2>Category List</h2>
        <div className="ui celled list" style={{ marginBottom: '48px'}}>{this.renderList()}</div>
        <Footer links={links}/>
      </div>
    );
  }
}

const mapStateToProps = (state) => {

  return{
    categories: Object.values(state.categories),
  };
}

export default connect(mapStateToProps, { readTextFile })(CategoryList);
